import React from 'react'
import {useState} from 'react'
import {graphql} from 'gatsby'
import Container from '../components/container'
import SearchBar from '../components/searchBar'

const filterPosts = require('../utils/filter')

const ProjectSearch = ({data}) => {
  const isBrowser = typeof window !== "undefined"
  const projects = data.allDatoCmsProject.edges

  const {search} = window.location ? window.location : ''

  const query = new URLSearchParams(search).get('s')

  const [searchQuery, setSearchQuery] = useState(query || '')
  const filteredPosts = filterPosts(projects, searchQuery)


  return (
    <Container>
      <SearchBar path="projects" searchQuery={searchQuery} setSearchQuery={setSearchQuery} />
    {filteredPosts.map(post => {
      return (
        <li key={post.node.title}>{post.node.title}</li>
      )
    })}
    </Container>
  )
}

export const projectquery = graphql`
  query getAllProjects {
    allDatoCmsProject {
      edges {
        node {
          title
          slug
        }
      }
    }
}
`;

export default ProjectSearch
